<template>
  <div>
    <el-row>
      <!--  -->
      <el-col :span="6">
        <el-card>
          <el-row>
            <el-row>
              <el-button type="primary" round style="width: 92%"></el-button>
            </el-row>
            <br />
            <el-button></el-button>
            <el-select v-model="taskFromValue" placeholder="">
              <el-option
                v-for="item in comTaskTitleList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-row>
          <br />
          <el-row>
            <el-button></el-button>
            <el-select v-model="taskTitileValue" placeholder="">
              <el-option
                v-for="item in tasktitleList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                :disabled="item.disabled"
              ></el-option>
            </el-select>
          </el-row>
          <br />
          <el-row>
            <el-button></el-button>
            <el-select v-model="isLang" placeholder="">
              <el-option
                v-for="item in langList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-row>
          <br />
          <el-row>
            <el-button round style="width: 92%">
              <em>:</em>
              {{ taskInfo.publicDate | filterTime }}
            </el-button>
          </el-row>
          <br />
          <el-row>
            <el-button type="danger" round style="width: 92%">
              <em>:</em>
              {{ taskInfo.deadLine | filterTime }}
            </el-button>
          </el-row>
        </el-card>
        <el-row>
          <el-card class="task-detail">
            <el-button type="primary" round style="width: 92%"></el-button>
            <el-row>{{ taskInfo.taskDetailValue }}</el-row>
          </el-card>
        </el-row>
      </el-col>
      <el-col :span="18">
        <CardTaskEditor :propsInfo="comTaskInfo" />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import CardTaskEditor from "./CardTaskEditor";
export default {
  name: "CardTask",
  components: {
    CardTaskEditor
  },
  data() {
    return {
      taskFromValue: "", //
      taskTitileValue: "", // 
      publicDate: "", // 
      taskList: [],
      tasktitleList: [],
      taskInfo: {},
      langList: [
        // codeEditor mode
        {
          value: "javascript",
          label: "javascript"
        },
        {
          value: "python",
          label: "python"
        },
        {
          value: "clike",
          label: "clike"
        },
        {
          value: "java",
          label: "java"
        }
      ],
      isLang: "javascript"
    };
  },
  computed: {
    comTaskTitleList() {
      let taskFromList = [];
      for (let i = 0; i < this.taskList.length; i++) {
        let oneTaskFrom = {};
        oneTaskFrom.value = this.taskList[i].teacherId;
        oneTaskFrom.label = this.taskList[i].teacherId;
        taskFromList.push(oneTaskFrom);
      }
      return taskFromList;
    },
    comTaskInfo() {
      let obj = {};
      obj.taskFromValue = this.taskFromValue
      obj.taskTitileValue = this.taskTitileValue;
      obj.isLang = this.isLang;
      obj.deadLine = this.taskInfo.deadLine
      return obj;
    }
  },

  watch: {
    taskFromValue: {
      handler(newVal, oldVal) {
        if (newVal) {
          this.tasktitleList = [];
          for (let i = 0; i < this.taskList.length; i++) {
            if (newVal === this.taskList[i].teacherId) {
              for (let j = 0; j < this.taskList[i].tasks.length; j++) {
                let oneTasktitle = {};
                oneTasktitle.value = this.taskList[i].tasks[j].taskName;
                oneTasktitle.label = this.taskList[i].tasks[j].taskName;
                this.tasktitleList.push(oneTasktitle);
              }
            }
          }
          return this.tasktitleList;
        }
      },
      deep: true
    },

    taskTitileValue: {
      handler(newVal, oldVal) {
        if (newVal) {
          for (let i = 0; i < this.taskList.length; i++) {
            if (this.taskList[i].teacherId === this.taskFromValue) {
              for (let j = 0; j < this.taskList[i].tasks.length; j++) {
                if (
                  this.taskList[i].tasks[j].taskName === this.taskTitileValue
                ) {
                  this.taskInfo.taskDetailValue = this.taskList[i].tasks[
                    j
                  ].supplement;
                  this.taskInfo.publicData = this.taskList[i].tasks[j].gmtCreat;
                  this.taskInfo.deadLine = this.taskList[i].tasks[j].deadline;
                }
              }
            }
          }
          return this.taskInfo;
        }
      }
    }
  },

  mounted() {
    this.$axios({
      url: "/api/user/selectTeacherTask",
      method: "get"
    })
      .then(res => {
        console.log(res);
        if (res.data.length > 0) {
          return (this.taskList = res.data);
        }
      })
      .catch(err => {
        alert("");
      });
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
.task-detail {
  height: 600px;
}
.el-icon-s-home {
  font-size: 300%;
  cursor: pointer;
  color: #409eff;
}
</style>