<template>
    <div>
        <el-card class="card">
            <el-row>
              <el-col :span="16">
                <el-input v-model="searchStuID" placeholder=""></el-input>
              </el-col>
              <el-col :span="2">
                <el-button type="primary" style="width: 100%" @click="handleSearchUserTask"></el-button>
              </el-col>
            </el-row>
            <br />
            <el-row>
              <el-table :data="searchUserTask" border style="width: 100%" max-height="600">
                <el-table-column fixed prop="taskName" label="" width="400"></el-table-column>
                <el-table-column prop="teacher" label="" width="120"></el-table-column>
                <el-table-column prop="gmtCreat" label="" width="180"></el-table-column>
                <el-table-column prop="deadline" label="" width="180"></el-table-column>
                <el-table-column prop="tag" label="" width="120"></el-table-column>
                <el-table-column fixed="right" label="" width="100">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-row>
        </el-card>
    </div>
</template>

<script>
export default {
  data() {
    return {
      searchStuID: '',
      searchUserTask: []
    }
  },
  methods: {
    handleSearchUserTask() {
      console.log('handleSearchUserTask');
      this.$axios({
      url: '/api/user/studentMessage',
      method: 'post',
      data: {
        stuId: this.searchStuID
      }
    })
    .then(res=>{
      console.log(res);
      if(res) {
        this.searchUserTask = res.data
      }
    })
    .catch(err=>{
      alert('');
    });
    }
  }
}
</script>

<style lang="scss" scoped>
    .card {
        width: 1240px;
        margin: 0 auto;
    }
</style>