<template>
    <div>
        <el-card class="card">
            <el-row>
              <el-col :span="16">
                <el-input v-model="searchTaskTitle" placeholder=""></el-input>
              </el-col>
              <el-col :span="2">
                <el-button type="primary" style="width: 100%" @click="handleSearchTask"></el-button>
              </el-col>
            </el-row>
            <br />
            <el-row>
              <el-table :data="searchTaskList" border style="width: 100%">
                <el-table-column fixed prop="taskName" label="" width="400"></el-table-column>
                <el-table-column prop="studentNumber" label="" width="120"></el-table-column>
                <el-table-column prop="gmtCreat" label="" width="180"></el-table-column>
                <el-table-column prop="deadline" label="" width="180"></el-table-column>
                <el-table-column prop="teacher" label="" width="120"></el-table-column>
                <el-table-column prop="tag" label="" width="120"></el-table-column>
                <el-table-column fixed="right" label="" width="100">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-row>
        </el-card>
    </div>
</template>
<script>
export default {
  data() {
    return {
      searchTaskTitle: '',
      searchTaskList: []
    }
  },
  methods: {
    handleSearchTask() {
      console.log('handleSearchTask');
      this.$axios({
        url: '/api/user/studentSelectTask',
        method: 'post',
        data: {
          taskName: this.searchTaskTitle
        }
      })
      .then(res=>{
        console.log(res);
        if(res) {
          return this.searchTaskList = res.data
        }
      })
      .catch(err=>{
        alert('');
      });
    }
  }
}
</script>
<style lang="scss" scoped>
    .card {
        width: 1280px;
        margin: 0 auto;
    }
</style>