<template>
  <div>
    <el-row>
      <el-col :span="18">
        <el-card :body-style="bodyStyle">
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
          >
            <el-menu-item index="1"></el-menu-item>
            <el-menu-item index="2"></el-menu-item>
            <el-menu-item index="3"></el-menu-item>
          </el-menu>
          <codemirror class="code-editor" :value="code" :options="editorOption" ref="myEditor"></codemirror>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card>
          <el-button type="primary" round style="width: 92%"></el-button>
          <ul>
            <li>Ctrl: </li>
          </ul>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-dialog title="" :visible.sync="centerDialogVisible" width="30%" center>
        <span>/</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitCode"> </el-button>
        </span>
      </el-dialog>
    </el-row>
  </div>
</template>

<script>
import { codemirror } from "vue-codemirror-lite";
/**
 * 
 *  java  clike 
 */
require("codemirror/mode/javascript/javascript");
require("codemirror/mode/vue/vue");
require("codemirror/mode/clike/clike");
require("codemirror/mode/python/python");
require("codemirror/mode/sql/sql");

// 
require("codemirror/addon/hint/anyword-hint.js");
require("codemirror/addon/hint/sql-hint.js");
require("codemirror/addon/hint/show-hint.js");
require("codemirror/addon/hint/show-hint.css");
require("codemirror/addon/hint/javascript-hint.js");
// 
require("codemirror/theme/ayu-dark.css");
require("codemirror/lib/codemirror.js");
require("codemirror/lib/codemirror.css");
export default {
  name: "CardTaskEditor",
  props: {
    propsInfo: {
      type: Object
    }
  },
  data() {
    return {
      centerDialogVisible: false,
      stuId: localStorage.getItem("msks_username"),
      bodyStyle: {
        padding: "0px"
      },
      activeIndex: "1",
      code: 'const str = "hello world"',
      editorOption: {
        lineNumbers: true,
        theme: "ayu-dark",
        mode: this.propsInfo.isLang,
        // mode: "javascript",
        // autofocus: false,
        lineWrapping: true,
        extraKeys: { Ctrl: "autocomplete" }
      }
    };
  },
  computed: {
    editor() {
      // get current editor object
      return this.$refs.myEditor.editor;
    },
    submitInfo() {
      let obj = {};
      obj.taskFromValue = this.propsInfo.taskFromValue
      obj.studentNumber = localStorage.getItem('msks_username');
      obj.taskTitileValue = this.propsInfo.taskTitileValue;
      obj.isLang = this.propsInfo.isLang;
      obj.code = this.code;
      obj.deadLine = this.propsInfo.deadLine
      return obj
    }
  },
  mounted() {
    // use editor object...
    this.editor.focus();
    this.editor.setSize("auto", "800px");
    console.log("this is current editor object", this.editor);
  },
  methods: {
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    handleSelect(key, keyPath) {
      switch (key) {
        case "1":
          // 
          console.log("chose 1");
          break;
        case "2":
          // 
          console.log("chose 2");
          break;
        case "3":
          // 
          console.log("chose 3");
          this.centerDialogVisible = true;
          break;
      }
    },
    submitCode() {
      console.log("submitCode");
      this.centerDialogVisible = false;
      this.$axios({
        url: '/api/user/studentCreatOrUpdateTask',
        method: 'post',
        data: {
          taskName: this.submitInfo.taskTitileValue,
          description: this.submitInfo.code,
          tag: this.submitInfo.isLang,
          studentNumber: this.submitInfo.studentNumber,
          teacher: this.submitInfo.taskFromValue,
          deadline: this.submitInfo.deadLine
        }
      })
      .then(res=>{
        console.log(res);
        if(res.data.msg === '') {
            this.$message.success(res.data.msg);
        } else if (res.data.msg === ''){
            this.$message.success(res.data.msg);
        } else {
            this.$message.warning(res.data.msg);
        }
      })
      .catch(err=>{
        alert('');
      });
    }
  }
};
</script>

